import React from 'react'
import "../styles/Right.css"
import man from "../assets/Ellipse 1.svg"
import statue from "../assets/img.svg"
const Right = () => {
  return (
    <div className='container'> 
          <img className='right-img' src={statue} alt="statue" />
      <div className='right-div'>
        <div className='first'>
            <p><strong>Golden human statue</strong></p>
            <p className='pay'>$8.625,61</p>
        </div>
        <div className='second'>
            <div className='person-div'>
                <img src={man} alt="img" />
                <p>George schmidt</p>
            </div>
            <p className='yellow'><strong>4.10 ETH</strong></p>
        </div>
    </div>
  </div> 
  )
}

export default Right
